/* CSS Reset */
html {
  min-width: 1200px;
  font-family: -apple-system, "Noto Sans", "Helvetica Neue", Helvetica, "Nimbus Sans L", Arial, "Liberation Sans", "PingFang SC", "Hiragino Sans GB", "Noto Sans CJK SC", "Source Han Sans SC", "Source Han Sans CN", "Microsoft YaHei", "Wenquanyi Micro Hei", "WenQuanYi Zen Hei", "ST Heiti", SimHei, "WenQuanYi Zen Hei Sharp", sans-serif;
  font-size: 14px;
  color: #162c5a;
  line-height: 1.5em;
  background-color: #f1f1f1;
  overflow-x: hidden;
}
body {
  margin: 0;
  min-height: 100vh;
}
a {
  color: #162c5a;
  text-decoration: none;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 1rem;
  margin: 0;
}
p {
  margin: 0;
}
ul {
  padding: 0;
}
li {
  list-style: none;
}
input,
textarea,
button {
  outline: none;
  border: none;
  resize: none;
}
*::-webkit-input-placeholder {
  color: #9a9a9a;
  font-weight: 400;
}
*::-ms-input-placeholder {
  color: #9a9a9a;
  font-weight: 400;
}
*::placeholder {
  color: #9a9a9a;
  font-weight: 400;
}
hr {
  display: block;
  width: 100%;
  height: 1px;
  border: 0;
  padding: 0;
  border-top: 1px dashed transparent;
  background: -webkit-gradient(linear, left top, left bottom, from(#fff), to(#fff)) padding-box, repeating-linear-gradient(-45deg, #ccc 0, #ccc 0.35em, #fff 0, #fff 0.65em);
  background: linear-gradient(#fff, #fff) padding-box, repeating-linear-gradient(-45deg, #ccc 0, #ccc 0.35em, #fff 0, #fff 0.65em);
}
